@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap);
.profile {
  background-color: #1d204b;
  border-radius: 20px;
  padding-bottom: 1vh;
}

.profile .profile-section {
  background-color: #5746ea;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  grid-gap: 1vh;
  gap: 1vh;
}

.profile .profile-image {
  margin: 5vh 2.5vw;
}
.profile .profile-image img {
  width: 40%;
  border-radius: 50%;
  background-color: bisque;
  border: 3px solid #e8e5fe;
}

.profile .profile-name {
  margin: 1vh 2.5vw;
  padding-bottom: 10vh;
}
.profile .profile-name p:nth-child(1) {
  margin: 0;
  color: #c1bdfe;
  font-size: 1em;
}

.profile .profile-name p:nth-child(2) {
  font-weight: 300;
  margin-top: 1vh;
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 0;
  color: #fcf5ff;
  font-size: 2.5em;
  width: 50%;
}

.profile .profile-selector {
  margin: 3vh 2.5vw;
  display: flex;
  flex-direction: column;
  grid-gap: 2.5vh;
  gap: 2.5vh;
}

.profile .profile-selector button {
  cursor: pointer;
  text-align: left;
  font-size: 1.2em;
  background-color: transparent;
  color: #5e619d;
  border: none;
  transition: ease-in 0.2s;
}

.profile .profile-selector button:hover {
  color: #e3e5ee;
}

@media only screen and (max-width: 750px) {
  .profile .profile-section {
    display: grid;
    grid-template-columns: 0.5fr 1fr;
    justify-content: center;
    align-items: center;
  }

  .profile .profile-image {
    width: 100%;
    margin: 5vh 5vw;
  }
  .profile .profile-image img {
    width: 80%;
    height: 100%;
    border-radius: 50%;
    border: 3px solid #e8e5fe;
  }

  .profile .profile-name {
    padding-top: 5vh;
    padding-bottom: 5vh;
  }
  .profile .profile-name p:nth-child(2) {
    width: 100%;
    font-size: 1.5em;
  }

  .profile .profile-selector {
    margin: 3vh 5vw;
    display: flex;

    flex-direction: row;
    justify-content: space-between;
  }

  .profile .profile-selector button {
    cursor: pointer;
    text-align: center;
    font-size: 1.2em;
    color: #5e619d;
    border: none;
    transition: ease-in 0.2s;
  }
}

.activity {
  color: white;
  border-radius: 15px;
}
.activity p {
  margin: 0;
}
.activity-background {
  border-radius: 15px;
  background-repeat: no-repeat;
  background-position: right;
}

.activity .activity-info {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  background-color: #1d204b;
  height: 80%;
  padding-left: 2vw;
  padding-right: 2vw;
}
.activity .activity-info .activity-name {
  padding-top: 4vh;

  font-size: 1.3em;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.activity .activity-info .activity-name .activity-satay img {
  cursor: pointer;
}
.activity .activity-info .activity-time {
  margin-top: 4vh;
}

.activity .activity-info .activity-time p:nth-child(1) {
  font-size: 2.5em;
}

.activity .activity-info .activity-time p:nth-child(2) {
  margin-top: 2vh;
  color: #c2c5f9;
  font-weight: 300;
}

@media only screen and (max-width: 750px) {
  .activity .activity-info {
    height: 100%;
  }

  .activity .activity-info .activity-time {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: 5vh;
  }

  .activity .activity-info {
    padding-left: 5vw;
    padding-right: 5vw;
  }
}

.activity-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1.5vw;
  gap: 1.5vw;
}

@media only screen and (max-width: 750px) {
  .activity-list {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10vh;
    gap: 10vh;
  }
}

html,
body {
  overflow-x: hidden;
}

body {
  background-color: #0d1323;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Rubik', sans-serif;
}

.dashboard {
  margin-top: 10vh;
  grid-column-gap: 3vw;
  -webkit-column-gap: 3vw;
          column-gap: 3vw;
  display: grid;
  grid-template-columns: 1fr 3fr;
  width: 75%;
  margin-left: auto;
  margin-right: auto;
}

@media only screen and (max-width: 750px) {
  .dashboard {
    grid-row-gap: 5vh;
    row-gap: 5vh;
    width: 85%;
    grid-template-columns: 1fr;
  }
}

